
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Group from 'vux/src/components/group/index.vue'

import {httpUrl} from '../../http_url';

export default {
name: 'HelloWorld',
data () {
return {
menus: {
menu1: '退出登录'
},
showMenus: false,
show: true,
time:60,
start: false,
password:'',
passwordAgain:''
}
},
components: {
XHeader,PopupHeader,Actionsheet,Box,
Group,
XInput,
XButton, Alert
},
methods: {
blurPassword(){
const _this = this;
if(this.isEmpty(this.password)){
this.$vux.alert.show({
title: '错误提示',
content: '请输入密码',
onHide () {

}
})
return
}else if(!this.Val.isPassWord(this.password) || this.password.length<6){
this.$vux.alert.show({
title: '错误提示',
content: '新密码必须为6位以上的英文和数字组成',
onHide () {
_this.password = ''
}
})
}
},
blurPasswordAgain(){
const _this = this;
if(this.isEmpty(this.passwordAgain)){
this.$vux.alert.show({
title: '错误提示',
content: '请输入密码',
onHide () {

}
})
return
}else if(this.passwordAgain != this.password){
this.$vux.alert.show({
title: '错误提示',
content: '两次密码输入不一致',
onHide () {
_this.passwordAgain = ''
}
})
}
},
submit(){
const _this = this;
let postData = this.$qs.stringify({
type:11,
password:this.password,
ckpassword:this.passwordAgain,
})
if(this.isEmpty(this.passwordAgain)){
this.$vux.alert.show({
title: '错误提示',
content: '请输入密码',
onHide () {

}
})
return
}else if(this.passwordAgain != this.password){
this.$vux.alert.show({
title: '错误提示',
content: '两次密码输入不一致',
onHide () {
_this.passwordAgain = ''
}
})
return
}else {
this.$axios({
method: 'post',
url:httpUrl.setPassword,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
data:postData
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.$vux.alert.show({
title: '提示',
content: '密码设置成功',
onHide () {
_this.$router.push('/home');
}
});
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录',
onHide () {
_this.$router.push('/quick_login');
}
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
}

},
goHome(){
this.$router.push('/home');
},
LoginOut(){
this.$axios({
method: 'post',
url:httpUrl.loginOut,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.$router.push('/quick_login');
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}

}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
},
filters: {

}
}



